import React, { Component } from 'react'
import './css.css';


class GraphVision extends Component {
   
    render(){
        console.log(this.props.update) 
        return ( 
        <div id="GraphVision_flyer">
             🦄<br></br>
             
             <div id="GraphVision_mainlabel">
             <center>🎭 HappyGraph Vision</center>
             </div>
             
             <div id="GraphVision_label">
             ⛳️ GOAL:
             <br></br>
             <br></br>
             Stimulating the growth of a pseudonymous social network inside DFOHUB
             <br></br>
             <br></br>
             🔫 Strategy:
             <br></br>
             <br></br>
             A social network based on an on-chain friendships ledger ⛓ is limited by the costs of transaction when you send or accept a new friendship.
             <br></br><br></br>
             🚜HappyFarm achieves the goal in a funny way:<br></br><br></br>
             By offering badges 🥇 to those who extend the social graph registering new friendships, and by offering Farming Bonuses 🍬 to those badges holders.
             <br></br>
             <br></br>
             A social graph we can browse would allow us to visualize the community and make 🚜HappyFarm a living place.
             <br></br>
             <br></br>
             We could later imagine to implement some kind of communication system along with the graph.
             <br></br>
             The 🎭HappyGraph is open and can be used by any experimental project.
             <br></br>
             <br></br>
                         <input  className="GraphVision_button" type="button" value="⚡️ BACK" onClick={() => this.props.update("HappyGraph")}/>
             </div>
        </div>
        );
    }


}




export default GraphVision