import React, { Component } from 'react'
import Web3 from 'web3';
import LockDrop from '../LockDrop/LockDrop';
import './css.css';


class Stake extends Component {
   

    componentDidMount() {
        this.loadBlockchainData()
    }

    async unstake(){
        window.ethereum.enable().then(async function (accounts) {
            const stakercontract = new web3.eth.Contract(periodicStakerABI, PERIODIC_STAKER)
            stakercontract.methods.unstake().send({gasPrice: '12345678', from: accounts[0]})
        })
    }

    async reset(){
        window.ethereum.enable().then(async function (accounts) {
            const stakercontract = new web3.eth.Contract(periodicStakerABI, PERIODIC_STAKER)
            stakercontract.methods.open().send({gasPrice: '12345678', from: accounts[0]})
        })
    }
    
    
        async loadBlockchainData() {
            window.ethereum.enable().then(async function (accounts) {
                let account=accounts[0];
                const beans = new web3.eth.Contract(periodicStakerABI, PERIODIC_STAKER) 
                let total_stake = await beans.methods.total_stake().call()
                document.getElementById("Stake_amount").innerHTML=(total_stake/1000000000000000000)+" 🌱"

                let total_stakers = await beans.methods.total_stakers().call()
                document.getElementById("Stakers_amount").innerHTML=total_stakers+" 🐧"

                let my_stake = await beans.methods.stake(accounts[0]).call()/1000000000000000000
            
                //document.getElementById("MyStake_amount").innerHTML=my_stake+" 🌱"

                

                let status = await beans.methods.status().call()
                let wait_blocks=0
                let status_icon='💚';
                if(status==0){
                    wait_blocks = await beans.methods.lockTime().call()
                    document.getElementById("Mystake_label").innerHTML='🔓 Yours:<span id="MyStake_amount" class="right">'+my_stake+' 🌱</span>'
                }
                if(status==1){
                    status_icon='💛';
                    wait_blocks = await beans.methods.lockTime().call()
                    let start_lock = await beans.methods.startLock().call()
                    let current_block = await web3.eth.getBlockNumber()
                    let endx=(parseInt(start_lock)+parseInt(wait_blocks)-parseInt(current_block))
                    if(endx<0)endx=0
                    if(endx>0)
                    document.getElementById("Mystake_label").innerHTML='🔐 Yours:<span id="MyStake_amount" class="right">'+my_stake+' 🌱</span>'
                    if(endx==0)
                    document.getElementById("Mystake_label").innerHTML='🔓 Yours:<span id="MyStake_amount" class="right">'+my_stake+' 🌱</span>'
                    
                }
                if(status==2){
                    status_icon='❤️';
                    wait_blocks = await beans.methods.freezeTime().call()
                    document.getElementById("Mystake_label").innerHTML='🔓 Yours:<span id="MyStake_amount" class="right">'+my_stake+' 🌱</span>'
                }

                let start_lockx = await beans.methods.startLock().call()
                let current_blockx = await web3.eth.getBlockNumber()
                let end=(parseInt(start_lockx)+parseInt(wait_blocks)-parseInt(current_blockx))
                if(end<0){
                    end=0
                    if(status!=0)status_icon='🖤'
                }
                
                
                document.getElementById("Endstake_label").innerHTML=status_icon+' Ends: <span id="EndStake_amount" class="right">'+end+' ⛓️</span>'
                
            
            })
        }

    render(){
        return ( 
        <div id="Staker_panel">
            <div id="Staker_mainlabel"><span id="stakecontract" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => jump()}>🐘 </span> Periodic Staking Contract</div>
            
            
            <div className="Staker_row">
                <div className="Stake_label">💰 Staked: <span id="Stake_amount" className="right">0.0 🌱</span></div>            
            </div>
            <div className="clear"/>

            <div className="Staker_row">
                <div className="Stakers_label">🎎 Stakers: <span id="Stakers_amount" className="right">0.0 🐧</span></div>            
            </div>
            <div className="clear"/>

            <div className="Mystake_row">
                <div id="Mystake_label" className="Mystake_label" onClick={() => this.unstake()}>🔓 Yours: <span id="MyStake_amount" className="right">0.0 🌱</span></div>            
            </div>
            <div className="clear"/>
            
            <div className="Mystake_row">
                <div id="Endstake_label" className="Mystake_label" onClick={() => this.reset()}>💙 Ends: <span id="EndStake_amount" className="right">0.0 ⛓️</span></div>            
            </div>
            <div className="clear"/>
           
            <br></br>
            <br></br>
            <center><img src="https://i.imgur.com/Qz7UEJy.png"></img></center>
            <br></br><br></br><br></br>
<LockDrop/>
        </div>
        );
    }


}

function jump(){
    window.open('https://etherscan.io/address/0x0d514b2f8e8009480edd0b1543a265e5b539b833')
}

function getAvatar(res){
    let avatar="👻";
    if(res.slice(-1)==="0")avatar="🐶";
    if(res.slice(-1)==="1")avatar="🐱";
    if(res.slice(-1)==="2")avatar="🐹";
    if(res.slice(-1)==="3")avatar="🐸";
    if(res.slice(-1)==="4")avatar="🐵";
    if(res.slice(-1)==="5")avatar="🐻";
    if(res.slice(-1)==="6")avatar="🐼";
    if(res.slice(-1)==="7")avatar="🐰";
    if(res.slice(-1)==="8")avatar="🐨";
    if(res.slice(-1)==="9")avatar="🐯";
    if(res.slice(-1)==="a")avatar="🦁";
    if(res.slice(-1)==="b")avatar="🐮";
    if(res.slice(-1)==="c")avatar="🐷";
    if(res.slice(-1)==="d")avatar="🐺";
    if(res.slice(-1)==="e")avatar="🐗";
    if(res.slice(-1)==="f")avatar="🐔";
    return avatar;
}

function getFamily(ress){
    let avatar="☠️";
    let res=ress.slice(-2)
    res = res.substring(0, 1);
    if(res.slice(-1)==="0")avatar="🐳";
    if(res.slice(-1)==="1")avatar="🦈";
    if(res.slice(-1)==="2")avatar="🐅";
    if(res.slice(-1)==="3")avatar="🐍";
    if(res.slice(-1)==="4")avatar="🐞";
    if(res.slice(-1)==="5")avatar="🐪";
    if(res.slice(-1)==="6")avatar="🦋";
    if(res.slice(-1)==="7")avatar="🐙";
    if(res.slice(-1)==="8")avatar="🐄";
    if(res.slice(-1)==="9")avatar="🐇";
    if(res.slice(-1)==="a")avatar="🐖";
    if(res.slice(-1)==="b")avatar="🐏";
    if(res.slice(-1)==="c")avatar="🐐";
    if(res.slice(-1)==="d")avatar="🦎";
    if(res.slice(-1)==="e")avatar="🐠";
    if(res.slice(-1)==="f")avatar="🦍";
    return avatar;
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

