import React, { Component } from 'react'
import './css.css';


class Math extends Component {

    render(){
        console.log(this.props.update) 
        return ( 
            <div id="Math_panel">
            <div id="Math_menu"><input  className="Math_menu_button" type="button" value="🦄 Vision" onClick={() => this.props.update("UnderConstruction")}/></div>
            <div id="Math_mainlabel"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🥗</span> Recipes</div>
            <div className="clear"></div>
            <div className="Equation">
                <div className="Math_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">💩</span></div>
                <div className="Math_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🐄+🌾=💩</span></div>
                <div className="clear"></div>
            </div>
            <div className="Equation">
                <div className="Math_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🌾</span></div>
                <div className="Math_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">⛰️+🌾=🌾</span></div>
                <div className="clear"></div>
            </div>
            <div className="Equation">
                <div className="Math_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🌾</span></div>
                <div className="Math_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">⛰️+🌾+💩=🌾🌾</span></div>
                <div className="clear"></div>
            </div>
            <div className="Equation">
                <div className="Math_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🌱</span></div>
                <div className="Math_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">⛰️+🌱=🌱</span></div>
                <div className="clear"></div>
            </div>
            <div className="Equation">
                <div className="Math_image"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">🌱</span></div>
                <div className="Math_label"><span aria-labelledby="jsx-a11y/accessible-emoji" role="img">⛰️+🌱+💩=🌱🌱</span></div>
                <div className="clear"></div>
            </div>
           <div id="Math_label_message">Under Construction!</div>   


           <div id="Math_footer">
                <input  className="Math_footer_button" type="button" value="💀  Res & Dev" onClick={() => this.props.update("Research")}/>       
                <input  className="Math_footer_button" type="button" value="🌾  Farming" onClick={() => this.props.update("HappyFarm")}/>       
            </div>
                 </div>
            
        );
    }


}




export default Math